.rk-footer{
  color: #fff;
  font-weight: 600;
  font-size: 22px;
  &__img{
    max-width: 220px;
    margin-left: auto;
    margin-right: auto;
    img{
      max-width: 100%;
    }
  }
  &__content{
    margin-top: -30px;
    background-color: #333333;
    padding: 20px 0;
    position: relative;
    z-index: 10;
  }

  a,
  a:hover{
    color: #fff;
    text-decoration: none;
  }
}

.ft-phone{
  font-weight: 600;
  font-size: 22px;
  margin-right: 15px;
}

.ft-soc-list{
  display: flex;
  align-items: center;
  &__item{
    display: block;
    width: 25px;
    height: 25px;
    margin-right: 10px;
    flex: 0 0 auto;
    img{
      max-width: 100%;
    }
  }
}